@import "parts/pro-badge";
@import "parts/vars";

.shipping-nova-poshta-education {
	&-manage-orders {
		margin-left: -20px;
		margin-right: 0;
		display: flex;
		height: 100vh;

		&-background {
			width: 100%;
			height: 100vh;
			background-image: url('../../img/education/manage-orders.png');
			background-position: 0 -35px;
		}

		.shipping-nova-poshta-education-popup {
			max-width: 400px;

			&-title,
			&-content {
				max-width: 350px;
				margin-left: auto;
				margin-right: auto;
			}
		}
	}

	&-popup {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translateX(-50%) translateY(-50%);
		background-color: #fff;
		padding: 35px;
		box-shadow: 0 0 60px 30px rgba(0, 0, 0, 0.15);
		border-radius: 10px;
		text-align: center;

		&-title {
			font-size: 20px;
			line-height: 1.5;
			margin: 0 0 16px 0;
			padding: 0;
			font-weight: bold;
		}

		&-content {
			font-size: 16px;
			color: #666;
			margin: 0 0 30px 0;
			padding: 0;
		}
	}

	&-button.button.button-primary {
		font-size: 20px;
		padding: 5px 30px;
		height: auto;
	}

	&-screenshot {
		max-width: 250px;
		margin: 0 15px 30px;
		text-align: center;
		position: relative;

		a {
			display: block;
			border: 5px solid #fff;
			transition: all 0.3s;
			box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.05);
			border-radius: 3px;
			margin-bottom: 5px;
			position: relative;

			&:before {
				content: '';
				display: block;
				transition: .3s ease;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: 1;
			}

			&:after {
				content: "\f179";
				font-family: 'dashicons', serif;
				color: #fff;
				display: block;
				transition: .3s ease;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translateX(-50%) translateY(-50%);
				z-index: 2;
				padding: 5px;
				width: 50px;
				height: 50px;
				font-size: 40px;
				line-height: 50px;
				border-radius: 50%;
				background-color: $primary;
				opacity: 0;
			}

			&:hover {
				&:before {
					background-color: rgba(0, 0, 0, .25);
				}

				&:after {
					opacity: 1;
				}
			}
		}

		figcaption {
			font-weight: 500;
			font-size: 12px;
			margin-top: 5px;
		}

		img {
			display: block;
			width: 100%;
			height: auto;
		}
	}

	&-description {
		font-size: 16px;
		max-width: 1000px;
	}

	&-screenshots {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -15px -30px -15px;
		max-width: 840px;
		justify-content: center;

		&-wrap {
			border-top: 1px solid $border;
			border-bottom: 1px solid $border;
			padding: 30px 0;
			margin: 25px 0 30px;
		}
	}
}

.titledesc {
	[for=shipping_nova_poshta_for_woocommerceshipping_nova_poshta_for_woocommerce_cod_enabled]:after,
	[for=shipping_nova_poshta_for_woocommerceshipping_nova_poshta_for_woocommerce_cod_title]:after,
	[for=shipping_nova_poshta_for_woocommerceshipping_nova_poshta_for_woocommerce_cod_prepayment]:after,
	[for=shipping_nova_poshta_for_woocommerceshipping_nova_poshta_for_woocommerce_cod_description]:after,
	[for=shipping_nova_poshta_for_woocommerceshipping_nova_poshta_for_woocommerce_cod_instructions]:after,
	[for=woocommerce_shipping_nova_poshta_for_woocommerce_free_shipping_from]:after,
	[for=woocommerce_shipping_nova_poshta_for_woocommerce_enable_free_shipping]:after,
	[for=woocommerce_shipping_nova_poshta_for_woocommerce_courier_title]:after,
	[for=woocommerce_shipping_nova_poshta_for_woocommerce_courier_free_shipping_from]:after,
	[for=woocommerce_shipping_nova_poshta_for_woocommerce_courier_enable_free_shipping]:after {
		content: '';
		top: 4px;
		@extend .shipping-nova-poshta-for-woocommerce-pro;
	}
}
